#footer {

	background-color: $dark-blue;
	padding: 30px 0;
}

.footer__main-content {
	border-bottom: 1px solid #919699;
	padding: 25px 0;

	@include media ('>=tablet') {
		 padding: 50px 0;
	}

	@include media ('>=desktop') {
		 padding: 80px 0;
	}

	.container {
		 @include media ('>=desktop') {
			  display: flex;
			  justify-content: space-between;
			  flex-wrap: wrap;
		 }
	}

	h3 {
		 position: relative;
		 color: $white;
		 font-weight: 700;
		 padding: 0 0 15px;
		 margin: 0 0 15px;

		 @include media ('>=tablet') {
			  font-size: 22px;
			  padding: 0 0 25px;
			  margin: 0 0 25px;
		 }

		 &:after {
			  content: " ";
			  position: absolute;
			  left: 0;
			  bottom: 0;
			  width: 52px;
			  height: 4px;
			  background-color: $green;
		 }
	}
}

.about-company {
	font-size: 15px;
	color: #b7c3c8;
	width: 100%;
	max-width: 800px;
	margin: 0 auto 30px;

	@include media ('>=tablet') {
		margin: 0 auto 50px;
	}

	@include media ('>=desktop') {
		 width: calc(29% - 80px);
		 margin: 0 80px 0 0;
	}
}

.footer__links {
	@include media ('>=tablet') {
		 width: 100%;
		 max-width: 800px;
		 margin: 0 auto;
	}

	@include media ('>=desktop') {
		 width: 71%;
	}
}

.footer__links-row {
	@include media ('>=phone') {
		 display: flex;
		 margin: 0 -10px;
		 flex-wrap: wrap;
	}
}

.footer__links-col {
  margin: 0 0 30px;

	@include media ('>=phone') {
		 width: 50%;
		 padding: 0 10px;

	}

	@include media ('>=tablet') {
		 width: 33.31%;
		 padding: 0 10px;
		 margin: 0;

	}

	&:last-child {
		 width: 100%;
		 margin: 0;

		 @include media ('>=tablet') {
			  width: 33.31%;
		 }
	}

}

// company-info
.company-info {

 @extend %listreset;

 li {
	 position: relative;
	 margin: 0 0 15px;
	 font-size: 14px;
	 color: $white;
	 padding: 0 0 0 30px;

	 &:hover {
		 a {
			 color: darken($white, 30%);
		 }
	 }

	 i {
		 position: absolute;
		 left: 0;
		 top: 0;
		 color: $green;
		 font-size: 15px;
	 }

	 a {
		 color: $white;
	 }

	 address {
		 font-style: normal;
	 }
 }
}

ul:not([class]):not([id]) {
 @extend %listreset;

 li {
	 position: relative;
	 margin: 0 0 10px;
	 padding: 0 0 0 20px;

	 &:before {
		 content: "\e904";
		 font-family: 'icomoon';
		 position: absolute;
		 left: 0;
		 top: 50%;
		 color: $green;
		 font-size: 12px;
		 transform: translateY(-50%) rotate(-180deg);
	 }

	 &:hover {
		 a {
			 color: darken($white, 30%);
		 }
	 }

	 a {
		 color: #b7c3c8;
	 }
 }
}

// form-newsletter
.form-newsletter {
	max-width: 257px;

	label {
		 font-size: 20px;
		 color: $white;
		 margin: 0 0 15px;
		 font-weight: 700;
		 display: block;
	}

	.input-outer {
		 display: flex;
	}

	.submit-outer {
		 width: 20%;
		 position: relative;

		 &:before {
			  content: "\e910";
			  font-family: 'icomoon';
			  color: $white;
			  position: absolute;
			  top: 50%;
			  left: 50%;
			  transform: translate(-50%, -50%);
			  font-size: 15px;
			  z-index: 2;
		 }
	}

	input[type=email] {
		 width: 80%;
		 background-color: #2f3c43;
		 border: 1px solid #192226;
		 border-bottom: 0;
		 color: $white;

		 &:focus {
			  border: 1px solid #192226;
			  border-bottom: 0;
		 }

		 &:active {
			  border: 1px solid #192226;
			  border-bottom: 0;
		 }

		 &:hover {
			  border: 1px solid #192226;
			  border-bottom: 0;
			  opacity: 0.8;
		 }
	}

	input[type=submit] {
		 background-color: $green;
		 position: relative;
		 font-size: 0;
		 width: 100%;
		 height: 100%;
		 border: none;
		 outline: none;
	}
}

// copy 
.copy {
	font-size: 15px;
	text-align: center;
	color: #919699;
	padding: 15px 0 0;

	@include media ('>=tablet') {
		 padding: 25px 0 0;
	}
}

