.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
    width: 100%;

}

.color-red {
    color: $red;
}

.btn {
    display: block;
    max-width: 205px;
    padding: $btn-padding;
    font-size: $btn-font-size;
    outline: none;
    border: none;
    color: $btn-color;
    background-color: $btn-background;
    line-height: 1;
    font-weight: 500;
    width: 100%;
    text-align: center;

    &.btn-red {
        background-color: $red;
    }

    &.btn-green {
        background-color: $green;
    }

    &.btn-small {
        width: 72px;
        font-size: 12px;
        padding: 10px 5px;
    }
}

.btn-register,
.btn-login {
    border-radius: 3px;

}

// section-search
.section-search {
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;

    h1 {
        @include media ('>=tablet') {
            font-size: 48px;
            font-weight: 700;
            color: $white;
            text-align: center;
        }
    }

    p {
            max-width: 800px;
            margin: 0 auto 30px;
            text-align: center;
        }
}